/** @format */

import { NavLink } from "react-router-dom";
import bot from "./imgs/logo.png";

const header = () => {
    return (
        <>
            <div
                className="container-fluid navbar-style py-3"
                style={{ backgroundColor: "#272934" }}
            >
                <div className="row">
                    <div className="col-md-10 col-12 mx-auto">
                        <nav
                            className="navbar navbar-expand-lg navbar-light"
                            style={{
                                backgroundColor: "#272934",
                            }}
                        >
                            <NavLink className="navbar-brand" id="logo" to="/">
                                Cyan
                            </NavLink>
                            <button
                                style={{
                                    color: "white",
                                    backgroundColor: "#fd5c63",
                                }}
                                class="navbar-toggler"
                                data-bs-toggle="collapse"
                                data-bs-target="#navbar"
                            >
                                <span class="navbar-toggler-icon"></span>
                            </button>

                            <div
                                className="navbar-collapse collapse"
                                id="navbar"
                            >
                                <ul className="navbar-nav ms-auto">
                                    <li className="nav-item active">
                                        <a
                                            className="nav-link"
                                            href="#serviceid"
                                        >
                                            Services
                                        </a>
                                    </li>
                                    <li className="nav-item active">
                                        <a
                                            className="nav-link"
                                            href="#featuresid"
                                        >
                                            Features
                                        </a>
                                    </li>
                                    <li className="nav-item active">
                                        <NavLink
                                            className="nav-link"
                                            to="/Gallery"
                                        >
                                            Gallery
                                        </NavLink>
                                    </li>
                                    <li className="nav-item active">
                                        <NavLink
                                            className="nav-link"
                                            to="/Contact"
                                        >
                                            Contact
                                        </NavLink>
                                    </li>
                                </ul>
                            </div>
                        </nav>
                    </div>
                </div>
            </div>
        </>
    );
};

export default header;
