/** @format */

import React, { useEffect, useState } from "react";
import { NavLink } from "react-router-dom";
import about from "./imgs/about.svg";
import growth from "./imgs/growth.svg";
import db from "./Firebase";

const Home = () => {
    const [userNo, setUserNo] = useState("");

    useEffect(() => {
        db.collection("users").onSnapshot((snapshot) => {
            setUserNo(snapshot.docs.length);
        });
    }, []);

    const redirect = () => {
        window.open("https://discord.com/login");
    };

    return (
        <div style={{ backgroundColor: "#272934" }}>
            {/* Main */}
            {/* hero section start */}
            <div
                style={{
                    height: "100vh",
                    display: "flex",
                    flexDirection: "column",
                    justifyContent: "center",
                }}
                className="container-fluid pb-5 pb-md-0"
                data-aos="fade-up"
                data-aos-delay="500"
                data-aos-duration="5000"
            >
                <div className="row">
                    <div className="col-md-10 col-12 mx-auto">
                        <div className="row">
                            {/*  left side --}} */}
                            <div className="col-md-6 col-12 hero-text order-md-0 order-1 d-flex justify-content-center align-items-start flex-column">
                                <h1>
                                    <span>CYAN</span>
                                </h1>
                                <h1>THE DISCORD BOT</h1>
                                <p>
                                    We Are The Team Of{" "}
                                    <span>Professional Developers</span>
                                </p>
                                <p>Current Users {userNo}</p>
                            </div>
                            {/* {{!-- right side --}} */}
                            <div className="col-md-6 col-12 hero-text order-md-1 order-0">
                                <figure>
                                    <img src={growth} alt="herosectionimage" />
                                </figure>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {/* hero section ends */}

            {/* Services start */}
            <div
                style={{
                    height: "100vh",
                    display: "flex",
                    flexDirection: "column",
                    justifyContent: "center",
                }}
                className="container-fluid mt-5 about-style pb-5 pb-md-0"
                data-aos="fade-up"
                data-aos-delay="500"
                data-aos-duration="5000"
                id="serviceid"
            >
                <div className="row">
                    <div className="col-md-10 col-12 mx-auto">
                        <h1 className="text-center main-heading">Services</h1>
                        <p className="text-center sub-heading">
                            Build the best Discord Server!
                        </p>
                        <div className="row">
                            {/* {{!-- left side --}} */}
                            <div className="col-md-6 col-12 hero-text">
                                <figure>
                                    <img src={about} alt="herosectionimage" />
                                </figure>
                            </div>
                            {/* {{!-- right side --}} */}
                            <div className="col-md-6 col-12 hero-text d-flex flex-column justify-content-center align-items-start">
                                <h1>Easily Integrate on your Server</h1>
                                <p>
                                    Configure moderation, leveling, Twitch
                                    alerts, and much more with the most
                                    easy-to-use dashboard!{" "}
                                </p>
                                <button
                                    onClick={redirect}
                                    className="btn-style btn-primary"
                                >
                                    Add to Discord
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {/* Services ends */}

            {/* Features start  */}
            <div
                className="container-fluid mt-5 about-style"
                data-aos="fade-up"
                data-aos-delay="500"
                data-aos-duration="5000"
                id="featuresid"
            >
                <div className="row">
                    <div className="col-md-10 col-12 mx-auto">
                        <h1 className="text-center main-heading">Features</h1>

                        <div className="row">
                            <div className="col-12">
                                <center>
                                    <img src="imgs/bot.png" alt="" />
                                </center>
                            </div>
                        </div>

                        <div className="row">
                            <div className="col-md-4">
                                <div className="box">
                                    <div className="our-services settings">
                                        <div className="icon">
                                            <img
                                                src="https://i.imgur.com/6NKPrhO.png"
                                                alt=""
                                            />
                                        </div>
                                        <h4>Custom Commands</h4>
                                        <p>
                                            Dashboard gives you full control to
                                            create the command of your dreams!
                                        </p>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-4 ">
                                <div className="box">
                                    <div className="our-services ssl">
                                        <div className="icon">
                                            <img
                                                src="https://i.imgur.com/6NKPrhO.png"
                                                alt=""
                                            />
                                        </div>
                                        <h4>Welcome Message</h4>
                                        <p>
                                            Take advantage of the welcome
                                            message to inform newcomers
                                        </p>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-4 ">
                                <div className="box">
                                    <div className="our-services database">
                                        <div className="icon">
                                            <img
                                                src="https://i.imgur.com/6NKPrhO.png"
                                                alt=""
                                            />
                                        </div>
                                        <h4>Levels and XP</h4>
                                        <p>
                                            Use our leveling system to identify
                                            and reward the most active member
                                        </p>
                                    </div>
                                </div>
                            </div>

                            <div className="col-md-4 ">
                                <div className="box">
                                    <div className="our-services speedup">
                                        <div className="icon">
                                            <img
                                                src="https://i.imgur.com/6NKPrhO.png"
                                                alt=""
                                            />
                                        </div>
                                        <h4>Highly customizable</h4>
                                        <p>
                                            Protect your server from ads, links,
                                            swearing, excessive emojis, and much
                                            more
                                        </p>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-4 ">
                                <div className="box">
                                    <div className="our-services privacy">
                                        <div className="icon">
                                            <img
                                                src="https://i.imgur.com/6NKPrhO.png"
                                                alt=""
                                            />
                                        </div>
                                        <h4>Announcement Plugins</h4>
                                        <p>
                                            Set up one of the various
                                            announcement plugins
                                        </p>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-4 ">
                                <div className="box">
                                    <div className="our-services backups">
                                        <div className="icon">
                                            <img
                                                src="https://i.imgur.com/6NKPrhO.png"
                                                alt=""
                                            />
                                        </div>
                                        <h4>Settings</h4>
                                        <p>
                                            Lorem ipsum dolor sit, amet
                                            consectetur adipisicing elit. Vel,
                                            sed?
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {/*  Features ends  */}

            {/*  Gallary Starts --}} */}
            <div
                style={{
                    height: "100vh",
                    display: "flex",
                    flexDirection: "column",
                    justifyContent: "center",
                }}
                className="container-fluid py-5 about-style"
                data-aos="fade-up"
                data-aos-delay="500"
                data-aos-duration="5000"
                id="gallery"
            >
                <div className="row">
                    <div className="col-md-10 col-12 mx-auto">
                        <h1 className="text-center main-heading">Gallery</h1>
                        <p
                            style={{ color: "whitesmoke" }}
                            className="text-center sub-heading"
                        >
                            Some ScreenShots of CYAN
                        </p>
                        <h1
                            style={{ fontSize: "15rem", color: "white" }}
                            className="text-center"
                        >
                            Hang On
                        </h1>
                        <br />
                        <center>
                            <button className="btn-style btn-primary my-5">
                                <NavLink
                                    to="/Gallery"
                                    style={{
                                        color: "white",
                                        textDecoration: "none",
                                    }}
                                >
                                    Navigate to Gallery
                                </NavLink>
                            </button>
                        </center>
                    </div>
                </div>
            </div>
            {/*  Gallary ends --}} */}
        </div>
    );
};

export default Home;
