/** @format */

import React, { useState } from "react";
import db from "./Firebase";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const Contact = () => {
    const [Name, setName] = useState("");
    const [email, setEmail] = useState("");

    const submitUser = (event) => {
        event.preventDefault();
        if (Name === "" && email === "") {
            toast.error("ðŸ˜Ž Invalid Input! ðŸ˜Ž", {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        } else {
            db.collection("users").add({
                name: Name,
                email: email,
            });

            toast.success("ðŸ¤ž Registered!! ðŸ¤ž", {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });

            setName("");
            setEmail("");
        }
    };

    return (
        <>
            {/*  Contact Us Starts --}} */}
            <div
                className="container-fluid about-style"
                id="contact"
                style={{
                    minHeight: "100vh",
                    display: "flex",
                    flexDirection: "column",
                    justifyContent: "center",
                }}
            >
                <div className="row">
                    <div className="col-md-10 col-12 mx-auto">
                        <h1 className="text-center main-heading">Contact Us</h1>
                        <p className="text-center sub-heading">
                            Feel Free To Contact Us
                        </p>
                        <div className="row">
                            {/*  left side --}} */}
                            <div className="col-md-6 col-12 hero-text">
                                <iframe
                                    src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d58665.836273018256!2d72.6105285326414!3d23.220705822254242!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x395c2b987c6d6809%3A0xf86f06a7873e0391!2z4KqX4Kq-4KqC4Kqn4KuA4Kqo4KqX4KqwLCDgqpfgq4HgqpzgqrDgqr7gqqQ!5e0!3m2!1sgu!2sin!4v1615132002372!5m2!1sgu!2sin"
                                    width="100%"
                                    height="450"
                                    style={{ border: 0 }}
                                    loading="lazy"
                                    title="Map"
                                ></iframe>
                            </div>
                            {/*  right side --}} */}
                            <div className="col-md-6 col-12 hero-text d-flex flex-column justify-content-center">
                                <center>
                                    <form>
                                        <div className="form-group">
                                            <label>Name</label>
                                            <input
                                                style={{ height: 30 }}
                                                type="text"
                                                value={Name}
                                                onChange={(event) =>
                                                    setName(event.target.value)
                                                }
                                                className="form-control"
                                                aria-describedby="emailHelp"
                                                name="name"
                                            />
                                        </div>
                                        <div className="form-group">
                                            <label>Email Address</label>
                                            <input
                                                style={{ height: 30 }}
                                                type="email"
                                                value={email}
                                                onChange={(event) =>
                                                    setEmail(event.target.value)
                                                }
                                                className="form-control"
                                                name="email"
                                            />
                                            <small
                                                id="emailHelp"
                                                className="form-text text-muted"
                                            >
                                                We'll never share your email
                                                with anyone else.
                                            </small>
                                        </div>
                                        <br />
                                        <button
                                            type="submit"
                                            onClick={submitUser}
                                            className="btn btn-primary button"
                                        >
                                            Submit
                                        </button>
                                    </form>
                                </center>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {/* -- Contact Us ends -- */}

            <ToastContainer />
        </>
    );
};

export default Contact;
