/** @format */
import React from "react";

const footer = () => {
    return (
        <>
            <div
                className="container-fluid navbar-style py-3"
                style={{ backgroundColor: "#272934", color: "white" }}
            >
                <div className="row">
                    <div className="col-md-10 col-12 mx-auto">
                        <ul className="navbar-nav ml-auto">
                            <center>
                                <h3>Made by Neh</h3>
                            </center>
                        </ul>
                    </div>
                </div>
            </div>
        </>
    );
};

export default footer;
